import { defaultProps, defaultOptions } from '../common/component-handler/press-component';
import { getRect, getStatusBarHeight } from '../common/dom/rect';
import { nextTick } from '../common/utils/system';

import utils from '../common/utils/utils';
import PressIcon from '../press-icon/press-icon.vue';

import computed from './computed';


export default {
  name: 'PressNavBar',
  options: {
    ...defaultOptions,
    styleIsolation: 'shared',
  },
  components: {
    PressIcon,
  },
  props: {
    ...defaultProps,
    title: { type: String, default: '' },
    fixed: {
      type: Boolean,
    },
    placeholder: {
      type: Boolean,
    },
    leftText: { type: String, default: '' },
    rightText: { type: String, default: '' },
    customStyle: { type: [String, Object], default: '' },
    leftArrow: Boolean,
    border: {
      type: Boolean,
      default: true,
    },
    zIndex: {
      type: Number,
      default: 1,
    },
    safeAreaInsetTop: {
      type: Boolean,
      default: true,
    },
    titleClass: {
      type: String,
      default: '',
    },
  },
  emits: ['click-left', 'click-right'],
  data() {
    return {
      height: 46,
    };
  },
  computed: {
    navBarClass() {
      const { border, fixed } = this;
      return `${utils.bem2('nav-bar', { fixed })} custom-class ${border ? 'press-hairline--bottom' : ''}`;
    },
    navBarStyle() {
      const { zIndex, statusBarHeight, safeAreaInsetTop, customStyle } = this;
      return computed.barStyle({ zIndex, statusBarHeight, safeAreaInsetTop, customStyle });
    },
  },
  watch: {
    fixed: {
      handler() {
        this.setHeight();
      },
    },
    placeholder: {
      handler() {
        this.setHeight();
      },
    },
  },
  created() {
    const statusBarHeight = getStatusBarHeight();
    this.statusBarHeight = statusBarHeight;
    this.height = 46 + statusBarHeight;
  },
  mounted() {
    this.setHeight();
  },
  methods: {
    onClickLeft() {
      this.$emit('click-left');
    },
    onClickRight() {
      this.$emit('click-right');
    },
    setHeight() {
      if (!this.fixed || !this.placeholder) {
        return;
      }
      nextTick(() => {
        getRect(this, '.press-nav-bar').then((res) => {
          if (res && 'height' in res) {
            this.height = res.height;
          }
        });
      });
    },
  },
};

